import React, { useState } from "react";
import "./taskForm.css";

export interface TaskFormProps {
  initialValues?: {
    title: string;
    description: string;
    status: "todo" | "in-progress" | "completed";
  };
  onSubmit: (values: {
    title: string;
    description: string;
    status: "todo" | "in-progress" | "completed";
  }) => void;
}

export function TaskForm({ initialValues, onSubmit }: TaskFormProps) {
  const [title, setTitle] = useState(initialValues?.title || "");
  const [description, setDescription] = useState(
    initialValues?.description || ""
  );
  const [status, setStatus] = useState(initialValues?.status || "todo");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit({ title, description, status });
  };

  return (
    <div style={{ margin: "3em" }} className="task-form">
      <form
        onSubmit={handleSubmit}
        style={{ display: "flex", flexDirection: "column", gap: "10px" }}
      >
        <input
          type="text"
          placeholder="Title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        <textarea
          placeholder="Description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
        <select
          value={status}
          onChange={(e) =>
            setStatus(e.target.value as "todo" | "in-progress" | "completed")
          }
        >
          <option value="todo">To Do</option>
          <option value="in-progress">In Progress</option>
          <option value="completed">Completed</option>
        </select>
        <button type="submit">Save</button>
      </form>
    </div>
  );
}
